<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="24">
						<el-form-item label="" prop="dianyingmingcheng">
							<el-input class="list_inp" v-model="form.dianyingmingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.dianyingmingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="haibao">
							<uploads
								:disabled="!isAdd||disabledForm.haibao?true:false"
								action="file/upload" 
								tip="" 
								:limit="3" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.haibao?form.haibao:''" 
								@change="haibaoUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="dianyingfenlei">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.dianyingfenlei?true:false"
								v-model="form.dianyingfenlei" 
								placeholder=""
								>
								<el-option v-for="(item,index) in dianyingfenleiLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="daoyan">
							<el-input class="list_inp" v-model="form.daoyan" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.daoyan?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="zhuyan">
							<el-input class="list_inp" v-model="form.zhuyan" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.zhuyan?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="shangyingriqi">
							<el-date-picker
								class="list_date"
								v-model="form.shangyingriqi"
								format="YYYY  MM  DD "
								value-format="YYYY-MM-DD"
								type="datetime"
								:readonly="!isAdd||disabledForm.shangyingriqi?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="pianzhang">
							<el-input class="list_inp" v-model="form.pianzhang" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.pianzhang?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="pingfen">
							<el-input class="list_inp" v-model.number="form.pingfen" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.pingfen?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="dianyingyugao">
							<uploads
								:disabled="!isAdd||disabledForm.dianyingyugao?true:false"
								type="file"
								action="file/upload" 
								tip="" 
								:limit="1" 
								style="width: 100%;text-align: left;"
								:fileUrls="form.dianyingyugao?form.dianyingyugao:''" 
								@change="dianyingyugaoUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="changci">
							<el-date-picker
								class="list_date"
								v-model="form.changci"
								format="YYYY-MM-DD HH:mm:ss"
								value-format="YYYY-MM-DD HH:mm:ss"
								type="datetime"
								:readonly="!isAdd||disabledForm.changci?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="fangyingting">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.fangyingting?true:false"
								v-model="form.fangyingting" 
								placeholder=""
								>
								<el-option v-for="(item,index) in fangyingtingLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="price">
							<el-input class="list_inp" v-model.number="form.price" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.price?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="" prop="number">
							<el-input class="list_inp" v-model.number="form.number" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.number?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="[,]" prop="selected">
							<el-input v-model="form.selected" placeholder="[,]" type="textarea"
							:readonly="!isAdd||disabledForm.selected?true:false"
							/>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="dianyingjianjie">
							<editor :value="form.dianyingjianjie" placeholder="" :readonly="!isAdd||disabledForm.dianyingjianjie?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'dianyingjianjie')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick"></el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'dianyingxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		dianyingmingcheng : false,
		haibao : false,
		dianyingfenlei : false,
		daoyan : false,
		zhuyan : false,
		shangyingriqi : false,
		pianzhang : false,
		pingfen : false,
		dianyingyugao : false,
		dianyingjianjie : false,
		changci : false,
		fangyingting : false,
		storeupnum : false,
		clicktime : false,
		price : false,
		number : false,
		selected : false,
		clicknum : false,
		thumbsupnum : false,
		crazilynum : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		dianyingmingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		haibao: [
		],
		dianyingfenlei: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		daoyan: [
		],
		zhuyan: [
		],
		shangyingriqi: [
		],
		pianzhang: [
		],
		pingfen: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		dianyingyugao: [
		],
		dianyingjianjie: [
		],
		changci: [
		],
		fangyingting: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicktime: [
		],
		price: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		number: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		selected: [
		],
		clicknum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		thumbsupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		crazilynum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//
	const haibaoUploadSuccess=(e)=>{
		form.value.haibao = e
	}
	//
	const dianyingfenleiLists = ref([])
	//
	const dianyingyugaoUploadSuccess=(e)=>{
		form.value.dianyingyugao = e
	}
	//
	const fangyingtingLists = ref([])
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			dianyingmingcheng: '',
			haibao: '',
			dianyingfenlei: '',
			daoyan: '',
			zhuyan: '',
			shangyingriqi: '',
			pianzhang: '',
			pingfen: '',
			dianyingyugao: '',
			dianyingjianjie: '',
			changci: '',
			fangyingting: '',
			storeupnum: '0',
			clicktime: '',
			price: '0',
			number: '',
			selected: '',
			clicknum: '0',
			thumbsupnum: '0',
			crazilynum: '0',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.dianyingjianjie = res.data.data.dianyingjianjie?(res.data.data.dianyingjianjie.replace(reg,'../../../cl8811483/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'logistics'){
			isAdd.value = false
			formTitle.value = ''
			getInfo()
		}else if(formType == 'reply'){
			type.value = formType
			isAdd.value = true
			disabledForm.value.cpicture = true
			disabledForm.value.content = true
			formTitle.value = ''
			getInfo()
		}else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='dianyingmingcheng'){
					form.value.dianyingmingcheng = row[x];
					disabledForm.value.dianyingmingcheng = true;
					continue;
				}
				if(x=='haibao'){
					form.value.haibao = row[x];
					disabledForm.value.haibao = true;
					continue;
				}
				if(x=='dianyingfenlei'){
					form.value.dianyingfenlei = row[x];
					disabledForm.value.dianyingfenlei = true;
					continue;
				}
				if(x=='daoyan'){
					form.value.daoyan = row[x];
					disabledForm.value.daoyan = true;
					continue;
				}
				if(x=='zhuyan'){
					form.value.zhuyan = row[x];
					disabledForm.value.zhuyan = true;
					continue;
				}
				if(x=='shangyingriqi'){
					form.value.shangyingriqi = row[x];
					disabledForm.value.shangyingriqi = true;
					continue;
				}
				if(x=='pianzhang'){
					form.value.pianzhang = row[x];
					disabledForm.value.pianzhang = true;
					continue;
				}
				if(x=='pingfen'){
					form.value.pingfen = row[x];
					disabledForm.value.pingfen = true;
					continue;
				}
				if(x=='dianyingyugao'){
					form.value.dianyingyugao = row[x];
					disabledForm.value.dianyingyugao = true;
					continue;
				}
				if(x=='dianyingjianjie'){
					form.value.dianyingjianjie = row[x];
					disabledForm.value.dianyingjianjie = true;
					continue;
				}
				if(x=='changci'){
					form.value.changci = row[x];
					disabledForm.value.changci = true;
					continue;
				}
				if(x=='fangyingting'){
					form.value.fangyingting = row[x];
					disabledForm.value.fangyingting = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='number'){
					form.value.number = row[x];
					disabledForm.value.number = true;
					continue;
				}
				if(x=='selected'){
					form.value.selected = row[x];
					disabledForm.value.selected = true;
					continue;
				}
				if(x=='clicknum'){
					form.value.clicknum = row[x];
					disabledForm.value.clicknum = true;
					continue;
				}
				if(x=='thumbsupnum'){
					form.value.thumbsupnum = row[x];
					disabledForm.value.thumbsupnum = true;
					continue;
				}
				if(x=='crazilynum'){
					form.value.crazilynum = row[x];
					disabledForm.value.crazilynum = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.price='0'
			form.value.clicknum='0'
			form.value.thumbsupnum='0'
			form.value.crazilynum='0'
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		context?.$http({
			url: `option/dianyingfenlei/dianyingfenlei`,
			method: 'get'
		}).then(res=>{
			dianyingfenleiLists.value = res.data.data
		})
		context?.$http({
			url: `option/fangyingting/fangyingting`,
			method: 'get'
		}).then(res=>{
			fangyingtingLists.value = res.data.data
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save=()=>{
		if(form.value.haibao!=null) {
			form.value.haibao = form.value.haibao.replace(new RegExp(context?.$config.url,"g"),"");
		}
		if(form.value.dianyingyugao!=null) {
			form.value.dianyingyugao = form.value.dianyingyugao.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		border-radius: 6px;
		padding: 30px;
		// form item
		:deep(.el-form-item) {
			margin: 0 0 20px 0;
			display: flex;
			//label
			.el-form-item__label {
			 color: #9E9E9E;
			 display: block;
			 width: 90px;
			 text-align: center;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 90px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				//
				.list_date {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
					}
				}
				// 
				.list_sel {
					border: 2px solid #5FB7FF;
					border-radius: 20px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					line-height: 36px;
					box-sizing: border-box;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
